@import"https://cdn.jsdelivr.net/npm/bootstrap-icons@1.11.2/font/bootstrap-icons.min.css";.card{width:240px;min-height:340px;box-shadow:0px 0px 5px 1px rgba(0,0,0,.1);border-radius:5px;border:0;transition:.4s;overflow:hidden}.card:hover{box-shadow:0px 0px 7px 1px rgba(0,0,0,.3)}.card__top{flex:0 0 220px;overflow:hidden}.card__img{width:100%;height:100%;object-fit:cover}.card__middle{display:flex;flex-direction:column}.card__title{display:flex;justify-content:center;align-items:center;text-align:center;padding:10px 0;margin-bottom:10px;font-size:19px;color:#000;text-decoration:none;min-height:74px;box-shadow:0px 1px 5px 1px rgba(0,0,0,.1),0px -1px 5px 1px rgba(0,0,0,.1)}.card__title:hover{color:crimson}.card__rating{text-align:center;font-style:italic;font-size:22px;margin-bottom:10px}.card__rating::before{content:"★"}.card__bottom{display:flex;justify-content:space-around;margin:0 10px 20px 10px}.card__price{font-weight:700;font-size:20px;line-height:43px}.card__price::after{content:"€"}.card__btn{border-radius:5px;background:#fff;padding:5px 35px 0;transition-duration:.4s;transition-timing-function:ease}.green-btn{border:1px solid #4bb44b;color:#4bb44b}.green-btn:hover{background:#4bb44b;color:#fff}.green-btn:focus{outline:none;box-shadow:0 0 0 3px rgba(23,207,45,.2)}.green-btn__true{background:#4bb44b;color:#fff}